      </view>
    </view>

    <!-- ÊêúÁ¥¢ÂíåÁ≠õÈÄâ -->
    <view class="search-section">
      <u-search
        v-model="searchKeyword"
        placeholder="ÊêúÁ¥¢‰ªªÂä°..."
