        class="add-btn"
      >
        Ê∑ªÂä†‰ªªÂä°
      </u-button>
    </view>

    <!-- ‰ªªÂä°ÂàóË°® -->
    <view class="task-list">
      <view v-if="tasks.length === 0" class="empty-state">
        <text class="empty-icon">üìù</text>
        <text class="empty-text">ÊöÇÊó†‰ªªÂä°</text>
        <text class="empty-desc">ÁÇπÂáª‰∏äÊñπÊåâÈíÆÊ∑ªÂä†ÊÇ®ÁöÑÁ¨¨‰∏Ä‰∏™‰ªªÂä°</text>
      </view>

      <view v-else>
        <view
          v-for="task in filteredTasks"
          :key="task.id"
          class="task-item"
          :class="{ completed: task.completed }"
        >
          <view class="task-content">
            <view class="task-header">
              <text class="task-title">{{ task.title }}</text>
              <view class="task-priority" :class="task.priority">
                {{ getPriorityText(task.priority) }}
              </view>
            </view>

            <text v-if="task.description" class="task-desc">
              {{ task.description }}
            </text>

            <view class="task-meta">
