            ‚úï
          </u-button>
        </view>

        <view class="popup-content">
          <u-form :model="newTask" label-position="top">
            <u-form-item label="‰ªªÂä°Ê†áÈ¢ò">
              <u-input
                v-model="newTask.title"
                placeholder="ËØ∑ËæìÂÖ•‰ªªÂä°Ê†áÈ¢ò"
                maxlength="50"
              ></u-input>
            </u-form-item>

            <u-form-item label="‰ªªÂä°ÊèèËø∞">
              <u-textarea
                v-model="newTask.description"
                placeholder="ËØ∑ËæìÂÖ•‰ªªÂä°ÊèèËø∞ÔºàÂèØÈÄâÔºâ"
                maxlength="200"
                :auto-height="true"
              ></u-textarea>
            </u-form-item>

