              ></u-input>
            </u-form-item>
          </u-form>
        </view>

        <view class="popup-actions">
          <u-button @click="showAddTask = false">ÂèñÊ∂à</u-button>
          <u-button type="primary" @click="addTask">‰øùÂ≠ò</u-button>
        </view>
