<script setup lang="ts">
import { ref, reactive, computed, onMounted } from "vue";

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const searchKeyword = ref("");
const showAddTask = ref(false);
const tasks = ref([
  {
    id: 1,
    title: "ÂÆåÊàêÈ°πÁõÆÊñáÊ°£",
    description: "ÁºñÂÜôAPIÊñáÊ°£ÂíåÁî®Êà∑ÊâãÂÜå",
    completed: false,
    priority: "high",
    dueDate: "2024-01-30",
    category: "Â∑•‰Ωú",
    createTime: new Date(),
  },
  {
    id: 2,
    title: "Ë¥≠‰π∞ÁîüÊ¥ªÁî®ÂìÅ",
    description: "ÂéªË∂ÖÂ∏ÇË¥≠‰π∞Êó•Áî®ÂìÅ",
    completed: true,
    priority: "medium",
    dueDate: "2024-01-28",
    category: "ÁîüÊ¥ª",
    createTime: new Date(),
  },
]);

// Êñ∞‰ªªÂä°Ë°®Âçï
const newTask = reactive({
  title: "",
  description: "",
  priority: "medium",
  dueDate: "",
  category: "",
});

// ‰ºòÂÖàÁ∫ßÈÄâÈ°π
const priorityOptions = [
  { label: "‰Ωé", value: "low" },
  { label: "‰∏≠", value: "medium" },
  { label: "È´ò", value: "high" },
  { label: "Á¥ßÊÄ•", value: "urgent" },
];

// ËÆ°ÁÆóÂ±ûÊÄß
const taskStats = computed(() => {
  const total = tasks.value.length;
  const completed = tasks.value.filter((task) => task.completed).length;
  const pending = total - completed;
  return { total, completed, pending };
});

const filteredTasks = computed(() => {
  if (!searchKeyword.value) return tasks.value;
  return tasks.value.filter(
    (task) =>
      task.title.toLowerCase().includes(searchKeyword.value.toLowerCase()) ||
      task.description.toLowerCase().includes(searchKeyword.value.toLowerCase())
  );
});

// ÊñπÊ≥ï
const handleSearch = () => {
  // ÊêúÁ¥¢ÈÄªËæëÂ∑≤Âú®ËÆ°ÁÆóÂ±ûÊÄß‰∏≠Â§ÑÁêÜ
};

const handleClearSearch = () => {
  searchKeyword.value = "";
};

const toggleTaskComplete = (taskId: number) => {
  const task = tasks.value.find((t) => t.id === taskId);
  if (task) {
    task.completed = !task.completed;
  }
};

const deleteTask = (taskId: number) => {
  uni.showModal({
    title: "Á°ÆËÆ§Âà†Èô§",
    content: "Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™‰ªªÂä°ÂêóÔºü",
    success: (res) => {
      if (res.confirm) {
        tasks.value = tasks.value.filter((t) => t.id !== taskId);
        uni.showToast({
          title: "Âà†Èô§ÊàêÂäü",
          icon: "success",
        });
      }
    },
  });
};

const addTask = () => {
  if (!newTask.title.trim()) {
    uni.showToast({
      title: "ËØ∑ËæìÂÖ•‰ªªÂä°Ê†áÈ¢ò",
      icon: "none",
    });
    return;
  }

  const task = {
    id: Date.now(),
    title: newTask.title.trim(),
    description: newTask.description.trim(),
    completed: false,
    priority: newTask.priority,
    dueDate: newTask.dueDate,
    category: newTask.category.trim() || "Êú™ÂàÜÁ±ª",
    createTime: new Date(),
  };

  tasks.value.unshift(task);

  // ÈáçÁΩÆË°®Âçï
  Object.assign(newTask, {
    title: "",
    description: "",
    priority: "medium",
    dueDate: "",
    category: "",
  });

  showAddTask.value = false;
  uni.showToast({
    title: "Ê∑ªÂä†ÊàêÂäü",
    icon: "success",
  });
};

const getPriorityText = (priority: string) => {
  const map: Record<string, string> = {
    low: "‰Ωé",
    medium: "‰∏≠",
    high: "È´ò",
    urgent: "Á¥ßÊÄ•",
  };
  return map[priority] || "‰∏≠";
};

const formatDate = (dateStr: string) => {
  const date = new Date(dateStr);
  return date.toLocaleDateString("zh-CN");
};

onMounted(() => {
  uni.__f__('log','at pages/tasks/tasks.vue:302',"‰ªªÂä°È°µÈù¢Âä†ËΩΩÂÆåÊàê");

  // Ê£ÄÊü•ÊòØÂê¶‰ªéÂØºËà™Ê†èÂàõÂª∫‰ªªÂä°ÊåâÈíÆËøõÂÖ•
  const pages = getCurrentPages();
  const currentPage = pages[pages.length - 1];
  const options = currentPage.options;

  if (options.action === "create") {
    // Âª∂ËøüÊòæÁ§∫ÂàõÂª∫‰ªªÂä°ÂºπÁ™óÔºåÁ°Æ‰øùÈ°µÈù¢ÂÆåÂÖ®Âä†ËΩΩ
    setTimeout(() => {
      showAddTask.value = true;
    }, 300);
  }
});
</script>

